"use client";

import { X } from "lucide-react";
import type { TaskType } from "@/lib/task-types";
import { TASK_TYPES } from "@/lib/task-types";

export type SortKey = "relevance" | "pay_desc" | "pay_asc" | "premium_first" | "near";

export type TaskFilters = {
  onlyHot: boolean;
  onlyPremium: boolean;
  types: TaskType[]; // –ø—É—Å—Ç–æ = –≤—Å–µ
  sort: SortKey;
};

export default function SortFilterModal({
  open,
  onClose,
  value,
  onChange
}: {
  open: boolean;
  onClose: () => void;
  value: TaskFilters;
  onChange: (next: TaskFilters) => void;
}) {
  if (!open) return null;

  const toggleType = (t: TaskType) => {
    const has = value.types.includes(t);
    const nextTypes = has ? value.types.filter((x) => x !== t) : [...value.types, t];
    onChange({ ...value, types: nextTypes });
  };

  const reset = () => {
    onChange({
      onlyHot: false,
      onlyPremium: false,
      types: [],
      sort: "relevance"
    });
  };

  return (
    <div className="fixed inset-0 z-50">
      <div className="absolute inset-0 bg-black/40" onClick={onClose} />
      <div className="absolute left-1/2 top-6 w-[min(560px,calc(100%-16px))] -translate-x-1/2">
        <div className="rounded-2xl border border-zinc-200 bg-white shadow-lg">
          <div className="flex items-start justify-between gap-3 border-b border-zinc-200 p-4">
            <div>
              <div className="text-sm text-zinc-500">–ó–∞–¥–∞–Ω–∏—è</div>
              <div className="text-base font-semibold">–°–æ—Ä—Ç–∏—Ä–æ–≤–∞—Ç—å –∏ —Ñ–∏–ª—å—Ç—Ä–æ–≤–∞—Ç—å</div>
            </div>

            <button
              onClick={onClose}
              className="rounded-xl border border-zinc-200 bg-white p-2"
              aria-label="–ó–∞–∫—Ä—ã—Ç—å"
            >
              <X className="h-5 w-5" />
            </button>
          </div>

          <div className="space-y-4 p-4">
            {/* –°–æ—Ä—Ç–∏—Ä–æ–≤–∫–∞ */}
            <div className="rounded-2xl border border-zinc-200 p-4">
              <div className="text-sm font-semibold">–°–æ—Ä—Ç–∏—Ä–æ–≤–∫–∞</div>

              <div className="mt-3 grid gap-2">
                {[
                  { k: "relevance", label: "–ü–æ —É–º–æ–ª—á–∞–Ω–∏—é" },
                  { k: "pay_desc", label: "–ü–æ —Ü–µ–Ω–µ: —Å–Ω–∞—á–∞–ª–∞ –¥–æ—Ä–æ–∂–µ" },
                  { k: "pay_asc", label: "–ü–æ —Ü–µ–Ω–µ: —Å–Ω–∞—á–∞–ª–∞ –¥–µ—à–µ–≤–ª–µ" },
                  { k: "premium_first", label: "–°–Ω–∞—á–∞–ª–∞ –≤—ã—Å–æ–∫–∏–π —Ç–∞—Ä–∏—Ñ" },
                  { k: "near", label: "–ü–æ –±–ª–∏–∑–æ—Å—Ç–∏ (–ø–æ–∫–∞ –∑–∞–≥–ª—É—à–∫–∞)" }
                ].map((x) => (
                  <button
                    key={x.k}
                    onClick={() => onChange({ ...value, sort: x.k as SortKey })}
                    className={[
                      "w-full rounded-xl border px-3 py-2 text-left text-sm",
                      value.sort === x.k
                        ? "border-zinc-900 bg-zinc-900 text-white"
                        : "border-zinc-200 bg-white hover:bg-zinc-50"
                    ].join(" ")}
                  >
                    {x.label}
                  </button>
                ))}
              </div>
            </div>

            {/* –ë—ã—Å—Ç—Ä—ã–µ —Ñ–∏–ª—å—Ç—Ä—ã */}
            <div className="rounded-2xl border border-zinc-200 p-4">
              <div className="text-sm font-semibold">–§–∏–ª—å—Ç—Ä—ã</div>

              <div className="mt-3 grid gap-2">
                <label className="flex items-center justify-between gap-3 rounded-xl border border-zinc-200 bg-white px-3 py-2 text-sm">
                  <span>–¢–æ–ª—å–∫–æ ‚Äú–≥–æ—Ä—è—â–∏–µ‚Äù</span>
                  <input
                    type="checkbox"
                    checked={value.onlyHot}
                    onChange={(e) => onChange({ ...value, onlyHot: e.target.checked })}
                    className="h-4 w-4"
                  />
                </label>

                <label className="flex items-center justify-between gap-3 rounded-xl border border-zinc-200 bg-white px-3 py-2 text-sm">
                  <span>–¢–æ–ª—å–∫–æ ‚Äú–≤—ã—Å–æ–∫–∏–π —Ç–∞—Ä–∏—Ñ‚Äù</span>
                  <input
                    type="checkbox"
                    checked={value.onlyPremium}
                    onChange={(e) => onChange({ ...value, onlyPremium: e.target.checked })}
                    className="h-4 w-4"
                  />
                </label>
              </div>

              <div className="mt-4 text-sm font-semibold">–¢–∏–ø –∑–∞–¥–∞–Ω–∏—è</div>
              <div className="mt-2 flex flex-wrap gap-2">
                {TASK_TYPES.map((t) => (
                  <button
                    key={t.value}
                    onClick={() => toggleType(t.value)}
                    className={[
                      "rounded-full border px-3 py-1 text-sm",
                      value.types.includes(t.value)
                        ? "border-zinc-900 bg-zinc-900 text-white"
                        : "border-zinc-200 bg-white hover:bg-zinc-50"
                    ].join(" ")}
                  >
                    {t.label}
                  </button>
                ))}
              </div>

              <div className="mt-2 text-xs text-zinc-500">
                –ï—Å–ª–∏ —Ç–∏–ø—ã –Ω–µ –≤—ã–±—Ä–∞–Ω—ã ‚Äî –ø–æ–∫–∞–∑—ã–≤–∞–µ–º –≤—Å–µ.
              </div>
            </div>

            {/* –î–µ–π—Å—Ç–≤–∏—è */}
            <div className="grid grid-cols-2 gap-2">
              <button
                onClick={reset}
                className="inline-flex items-center justify-center rounded-xl border border-zinc-200 bg-white px-4 py-3 text-sm font-medium text-zinc-900"
              >
                –°–±—Ä–æ—Å–∏—Ç—å
              </button>
              <button
                onClick={onClose}
                className="inline-flex items-center justify-center rounded-xl bg-zinc-900 px-4 py-3 text-sm font-medium text-white"
              >
                –ü—Ä–∏–º–µ–Ω–∏—Ç—å
              </button>
            </div>

            <div className="text-xs text-zinc-500">
              UI-only: ‚Äú–±–ª–∏–∑–æ—Å—Ç—å‚Äù —Å–µ–π—á–∞—Å —Å—á–∏—Ç–∞–µ—Ç—Å—è —É—Å–ª–æ–≤–Ω–æ (–¥–µ—Ç–µ—Ä–º–∏–Ω–∏—Ä–æ–≤–∞–Ω–Ω–æ –ø–æ –≥–æ—Ä–æ–¥—É/–∞–¥—Ä–µ—Å—É).
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
