"use client";

import { useEffect, useMemo, useState } from "react";
import DayTabs from "@/components/day-tabs";
import type { Slot } from "@/lib/slots";
import { getBooking, setBooking } from "@/lib/booking-state";
import { formatMoneyRub } from "@/lib/slots";

type Range = { start: number; end: number }; // minutes

function parseRangeMinutes(timeRange: string): Range {
  // "08:00‚Äì15:00"
  const [aRaw, bRaw] = timeRange.split("‚Äì").map((x) => x.trim());
  const [ah, am] = (aRaw ?? "00:00").split(":").map(Number);
  const [bh, bm] = (bRaw ?? "00:00").split(":").map(Number);
  const start = (ah || 0) * 60 + (am || 0);
  const end = (bh || 0) * 60 + (bm || 0);
  return { start, end };
}

function overlaps(a: Range, b: Range) {
  return a.start < b.end && b.start < a.end;
}

export default function BookingModal({
  open,
  onClose,
  days,
  slots,
  hotDays,
  premiumDays,
  initialDay,
  initialTitle
}: {
  open: boolean;
  onClose: () => void;
  days: string[];
  slots: Slot[];
  hotDays: Set<string>;
  premiumDays: Set<string>;
  initialDay: string;
  initialTitle?: string;
}) {
  const [day, setDay] = useState(initialDay);
  const [selected, setSelected] = useState<string[]>([]);
  const [onlyConsecutive, setOnlyConsecutive] = useState(true);
  const [error, setError] = useState<string | null>(null);

  // reset –ø—Ä–∏ –æ—Ç–∫—Ä—ã—Ç–∏–∏
  useEffect(() => {
    if (open) {
      setDay(initialDay);
      setSelected([]);
      setOnlyConsecutive(true);
      setError(null);
    }
  }, [open, initialDay]);

  const daySlots = useMemo(() => {
    const list = slots.filter((s) => s.date === day);
    return list.sort((a, b) => parseRangeMinutes(a.time).start - parseRangeMinutes(b.time).start);
  }, [slots, day]);

  const idsOrdered = useMemo(() => daySlots.map((s) => s.id), [daySlots]);

  const daySlotById = useMemo(() => {
    const m = new Map<string, Slot>();
    for (const s of daySlots) m.set(s.id, s);
    return m;
  }, [daySlots]);

  const selectedSlots = useMemo(
    () => selected.map((id) => daySlotById.get(id)).filter(Boolean) as Slot[],
    [selected, daySlotById]
  );

  const totalPay = useMemo(() => selectedSlots.reduce((sum, s) => sum + s.pay, 0), [selectedSlots]);

  const isBooked = (slotId: string) => getBooking(slotId).status === "booked";

  // –ß–ò–°–¢–ê–Ø –ø—Ä–æ–≤–µ—Ä–∫–∞: –º–æ–∂–Ω–æ –ª–∏ –¥–æ–±–∞–≤–∏—Ç—å —Å–ª–æ—Ç –±–µ–∑ –ø–µ—Ä–µ—Å–µ—á–µ–Ω–∏–π —Å –≤—ã–±—Ä–∞–Ω–Ω—ã–º–∏/—É–∂–µ –∑–∞–±—Ä–æ–Ω–∏—Ä–æ–≤–∞–Ω–Ω—ã–º–∏
  const canSelectWithoutOverlap = (slotId: string) => {
    const slot = daySlotById.get(slotId);
    if (!slot) return false;

    const r = parseRangeMinutes(slot.time);

    // 1) –Ω–µ –ø–µ—Ä–µ—Å–µ–∫–∞—Ç—å—Å—è —Å –≤—ã–±—Ä–∞–Ω–Ω—ã–º–∏
    for (const sid of selected) {
      if (sid === slotId) continue;
      const other = daySlotById.get(sid);
      if (!other) continue;
      if (overlaps(r, parseRangeMinutes(other.time))) return false;
    }

    // 2) –Ω–µ –ø–µ—Ä–µ—Å–µ–∫–∞—Ç—å—Å—è —Å —É–∂–µ booked –≤ —ç—Ç–æ—Ç –¥–µ–Ω—å
    for (const s of daySlots) {
      if (!isBooked(s.id)) continue;
      if (s.id === slotId) continue;
      if (overlaps(r, parseRangeMinutes(s.time))) return false;
    }

    return true;
  };

  // –ß–ò–°–¢–ê–Ø –ø—Ä–æ–≤–µ—Ä–∫–∞: —Ä–µ–∂–∏–º ‚Äú–ø–æ–¥—Ä—è–¥‚Äù (—Ç–æ–ª—å–∫–æ —Å–æ—Å–µ–¥–Ω–∏–µ –ø–æ —Å–ø–∏—Å–∫—É)
  const canAddConsecutive = (slotId: string) => {
    if (!onlyConsecutive) return true;
    if (selected.length === 0) return true;

    const idx = idsOrdered.indexOf(slotId);
    if (idx === -1) return false;

    const selectedIdxs = selected
      .map((x) => idsOrdered.indexOf(x))
      .filter((x) => x >= 0)
      .sort((a, b) => a - b);

    const min = selectedIdxs[0];
    const max = selectedIdxs[selectedIdxs.length - 1];
    return idx === min - 1 || idx === max + 1;
  };

  // –ß–ò–°–¢–ê–Ø –ø—Ä–æ–≤–µ—Ä–∫–∞: –º–æ–∂–Ω–æ –ª–∏ –≤—ã–±—Ä–∞—Ç—å (–Ω–µ booked, –Ω–µ –ø–µ—Ä–µ—Å–µ–∫–∞–µ—Ç—Å—è, –∏ –ø–æ–¥—Ä—è–¥ –µ—Å–ª–∏ –≤–∫–ª—é—á–µ–Ω–æ)
  const canSelect = (slotId: string) => {
    if (selected.includes(slotId)) return true; // —Å–Ω—è—Ç–∏–µ —Ä–∞–∑—Ä–µ—à–∞–µ–º –≤—Å–µ–≥–¥–∞
    if (isBooked(slotId)) return false;
    if (!canSelectWithoutOverlap(slotId)) return false;
    if (!canAddConsecutive(slotId)) return false;
    return true;
  };

  const toggle = (slotId: string) => {
    setSelected((prev) => {
      // —Å–Ω—è—Ç—å
      if (prev.includes(slotId)) {
        setError(null);
        return prev.filter((x) => x !== slotId);
      }

      // –¥–æ–±–∞–≤–∏—Ç—å
      if (!canSelect(slotId)) {
        // –æ–±—ä—è—Å–Ω—è–µ–º –ø—Ä–∏—á–∏–Ω—É
        if (isBooked(slotId)) {
          setError("–≠—Ç–æ—Ç —Å–ª–æ—Ç —É–∂–µ –∑–∞–±—Ä–æ–Ω–∏—Ä–æ–≤–∞–Ω.");
        } else if (!canSelectWithoutOverlap(slotId)) {
          setError("–ù–µ–ª—å–∑—è –≤—ã–±—Ä–∞—Ç—å: —Å–ª–æ—Ç –ø–µ—Ä–µ—Å–µ–∫–∞–µ—Ç—Å—è –ø–æ –≤—Ä–µ–º–µ–Ω–∏ —Å –≤—ã–±—Ä–∞–Ω–Ω—ã–º –∏–ª–∏ —É–∂–µ –∑–∞–±—Ä–æ–Ω–∏—Ä–æ–≤–∞–Ω–Ω—ã–º.");
        } else if (!canAddConsecutive(slotId)) {
          setError("–í —Ä–µ–∂–∏–º–µ ‚Äú–ø–æ–¥—Ä—è–¥‚Äù –º–æ–∂–Ω–æ –¥–æ–±–∞–≤–ª—è—Ç—å —Ç–æ–ª—å–∫–æ —Å–æ—Å–µ–¥–Ω–∏–µ —Å–ª–æ—Ç—ã (—Å–ª–µ–≤–∞/—Å–ø—Ä–∞–≤–∞).");
        } else {
          setError("–ù–µ–ª—å–∑—è –≤—ã–±—Ä–∞—Ç—å —ç—Ç–æ—Ç —Å–ª–æ—Ç.");
        }
        return prev;
      }

      setError(null);
      return [...prev, slotId];
    });
  };

  if (!open) return null;

  return (
    <div className="fixed inset-0 z-50">
      <div className="absolute inset-0 bg-black/40" onClick={onClose} />
      <div className="absolute left-1/2 top-4 w-[min(560px,calc(100%-16px))] -translate-x-1/2">
        <div className="rounded-2xl border border-zinc-200 bg-white shadow-lg">
          <div className="flex items-start justify-between gap-3 border-b border-zinc-200 p-4">
            <div>
              <div className="text-sm text-zinc-500">–ó–∞–ø–∏—Å—å –Ω–∞ —Å–ª–æ—Ç—ã</div>
              <div className="text-base font-semibold">{initialTitle ?? "–í—ã–±–µ—Ä–∏ –¥–∞—Ç—É –∏ —Å–ª–æ—Ç—ã"}</div>
            </div>
            <button onClick={onClose} className="rounded-xl border border-zinc-200 bg-white px-3 py-2 text-sm">
              –ó–∞–∫—Ä—ã—Ç—å
            </button>
          </div>

          <div className="space-y-3 p-4">
            <DayTabs
              days={days}
              value={day}
              onChange={(d) => {
                setDay(d);
                setSelected([]);
                setError(null);
              }}
              hotDays={hotDays}
              premiumDays={premiumDays}
            />

            <div className="flex items-center justify-between gap-3 rounded-2xl border border-zinc-200 bg-white p-3">
              <div>
                <div className="text-sm font-medium">–í—ã–±–∏—Ä–∞—Ç—å ‚Äú–ø–æ–¥—Ä—è–¥‚Äù</div>
                <div className="text-xs text-zinc-500">–í–∫–ª ‚Äî —Ç–æ–ª—å–∫–æ —Å–æ—Å–µ–¥–Ω–∏–µ —Å–ª–æ—Ç—ã, –ø–ª—é—Å –∑–∞–ø—Ä–µ—Ç –ø–µ—Ä–µ—Å–µ—á–µ–Ω–∏–π –ø–æ –≤—Ä–µ–º–µ–Ω–∏.</div>
              </div>
              <button
                onClick={() => {
                  setOnlyConsecutive((v) => !v);
                  setSelected([]);
                  setError(null);
                }}
                className="rounded-xl border border-zinc-200 bg-white px-3 py-2 text-sm"
              >
                {onlyConsecutive ? "–í–∫–ª" : "–í—ã–∫–ª"}
              </button>
            </div>

            {error ? (
              <div className="rounded-2xl border border-rose-200 bg-rose-50 p-3 text-sm text-rose-800">
                {error}
              </div>
            ) : null}

            {daySlots.length === 0 ? (
              <div className="rounded-2xl border border-zinc-200 bg-white p-4 text-sm text-zinc-600">
                –ù–∞ —ç—Ç–æ—Ç –¥–µ–Ω—å —Å–ª–æ—Ç–æ–≤ –Ω–µ—Ç. –õ–∏—Å—Ç–∞–π —Ç–∞–±—ã —Å–≤–µ—Ä—Ö—É.
              </div>
            ) : (
              <div className="space-y-2">
                {daySlots.map((s) => {
                  const checked = selected.includes(s.id);
                  const booked = isBooked(s.id);

                  // –í–ê–ñ–ù–û: canSelect() –ß–ò–°–¢–ê–Ø –∏ –ù–ï setState
                  const disabled = !checked && !canSelect(s.id);

                  return (
                    <button
                      key={s.id}
                      onClick={() => toggle(s.id)}
                      disabled={disabled}
                      className={[
                        "w-full rounded-2xl border p-4 text-left transition",
                        checked ? "border-zinc-900 bg-zinc-50" : "border-zinc-200 hover:bg-zinc-50",
                        disabled ? "opacity-50 cursor-not-allowed" : ""
                      ].join(" ")}
                      title={booked ? "–£–∂–µ –∑–∞–ø–∏—Å–∞–Ω –Ω–∞ —ç—Ç–æ—Ç —Å–ª–æ—Ç" : undefined}
                    >
                      <div className="flex items-start justify-between gap-3">
                        <div>
                          <div className="flex items-center gap-2">
                            <span
                              className={[
                                "inline-flex h-5 w-5 items-center justify-center rounded-md border text-xs",
                                checked ? "border-zinc-900 bg-zinc-900 text-white" : "border-zinc-300 bg-white"
                              ].join(" ")}
                            >
                              {checked ? "‚úì" : ""}
                            </span>

                            <div className="font-semibold">
                              {s.title}
                              {s.hot ? <span className="ml-2 text-xs text-red-600">‚Ä¢ –≥–æ—Ä—è—á–∏–π</span> : null}
                              {s.pay >= 3500 ? <span className="ml-2 text-xs text-sky-700">‚Ä¢ –≤—ã—Å–æ–∫–∏–π —Ç–∞—Ä–∏—Ñ</span> : null}
                              {booked ? <span className="ml-2 text-xs text-emerald-700">‚Ä¢ —É–∂–µ –∑–∞–ø–∏—Å–∞–Ω</span> : null}
                            </div>
                          </div>

                          <div className="mt-1 text-sm text-zinc-600">
                            {s.company} ¬∑ {s.city}
                          </div>
                        </div>

                        <div className="text-right">
                          <div className="text-sm font-semibold">{s.time}</div>
                          <div className="text-xs text-zinc-500">{formatMoneyRub(s.pay)}</div>
                        </div>
                      </div>
                    </button>
                  );
                })}
              </div>
            )}

            <div className="rounded-2xl border border-zinc-200 bg-white p-4 text-sm">
              <div className="flex items-center justify-between">
                <span className="text-zinc-600">–í—ã–±—Ä–∞–Ω–æ —Å–ª–æ—Ç–æ–≤:</span>
                <span className="font-semibold">{selected.length}</span>
              </div>
              <div className="mt-1 flex items-center justify-between">
                <span className="text-zinc-600">–ò—Ç–æ–≥–æ –æ–ø–ª–∞—Ç–∞:</span>
                <span className="font-semibold">{formatMoneyRub(totalPay)}</span>
              </div>
            </div>

            <button
              disabled={selected.length === 0}
              onClick={() => {
                // —Ñ–∏–Ω–∞–ª—å–Ω–∞—è –ø—Ä–æ–≤–µ—Ä–∫–∞ –ø–µ—Ä–µ—Å–µ—á–µ–Ω–∏–π –≤–Ω—É—Ç—Ä–∏ selected (–Ω–∞ –≤—Å—è–∫–∏–π)
                const ranges = selectedSlots.map((s) => ({ id: s.id, r: parseRangeMinutes(s.time) }));
                for (let i = 0; i < ranges.length; i++) {
                  for (let j = i + 1; j < ranges.length; j++) {
                    if (overlaps(ranges[i].r, ranges[j].r)) {
                      setError("–ï—Å—Ç—å –ø–µ—Ä–µ—Å–µ–∫–∞—é—â–∏–µ—Å—è –ø–æ –≤—Ä–µ–º–µ–Ω–∏ —Å–ª–æ—Ç—ã. –£–±–µ—Ä–∏ –∫–æ–Ω—Ñ–ª–∏–∫—Ç –∏ –ø–æ–ø—Ä–æ–±—É–π —Å–Ω–æ–≤–∞.");
                      return;
                    }
                  }
                }

                for (const id of selected) setBooking(id, "booked");
                onClose();
              }}
              className="inline-flex w-full items-center justify-center rounded-xl bg-zinc-900 px-4 py-3 text-sm font-medium text-white disabled:opacity-50"
            >
              –ü–æ–¥—Ç–≤–µ—Ä–¥–∏—Ç—å –∑–∞–ø–∏—Å—å ({selected.length})
            </button>

            <div className="text-xs text-zinc-500">
              UI-only: –±—Ä–æ–Ω—å —Å–æ—Ö—Ä–∞–Ω—è–µ—Ç—Å—è –≤ localStorage. –ü–µ—Ä–µ—Å–µ—á–µ–Ω–∏—è –ø–æ –≤—Ä–µ–º–µ–Ω–∏ –∑–∞–ø—Ä–µ—â–µ–Ω—ã.
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
